.slick-slider {
  position: relative;
  display: block;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-touch-callout: none;
  -khtml-user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  display: block;
  overflow: hidden;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  top: 0;
  left: 0;
  display: block;
}

.slick-track:before,
.slick-track:after {
  display: table;
  content: "";
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  display: none;
  float: left;
  height: 100%;
  min-height: 1px;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

/* ARROW NAVIGATION */

.slick-arrow {
  position: absolute;
  z-index: 2;
  top: 50%;
  transform: translateY(-50%);
  color: $slate;
  opacity: 0.64;
  font-size: 32px;
  line-height: 24px;
  background: transparent;
}

.slick-arrow:hover {
  color: $bright-red;
  opacity: 1;
}

.slick-arrow--previous {
  left: -27px;
}

.slick-arrow--next {
  right: -27px;
}

@include phone {
  .slick-arrow {
    font-size: 32px;
  }

  .slick-arrow--previous {
    left: -22px;
  }

  .slick-arrow--next {
    right: -22px;
  }
}

/* DOTS NAVIGATION */

.slick-dots {
  position: absolute;
  bottom: 48px;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  font-size: 0;
  line-height: 0;
}

.slick-dots li {
  margin: 0 4px;
}

.slick-dots button {
  padding: 0;
  text-indent: -999em;
  width: 12px;
  height: 12px;
  border: 1px solid $color-white;
  background-color: transparent;
  border-radius: 100px;
}

.slick-dots .slick-active button {
  background-color: $color-white;
}
